@charset 'UTF-8';

.single {
  header:not(.fixed) {
    nav {
      a {
        color: #fff;
      }
    }

    .logo {
      &__black {
        display: none;
      }
      &__white {
        display: block;
      }
    }
  }

  main {
    position: relative;
    overflow: hidden;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 0;
      height: 0;
      border-top: solid 0 transparent;
      border-left: solid 0 transparent;
      border-right: solid 600px $secondary-color;
      border-bottom: solid 400px transparent;
      z-index: 2;

      @include phone {
        border-right: solid 200px #2ea6d0;
        border-bottom: solid 100px transparent;
      }
    }
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 0;
      border-top: solid 0 transparent;
      border-left: solid 2000px #273042;
      border-right: solid 0 transparent;
      border-bottom: solid 900px transparent;
      z-index: 0;

      @include phone {
        border-bottom: solid 715px transparent;
      }
    }
  }

  section.welcome {
    padding-top: 70px;
    height: 100vh;
    max-height: 665px;
    position: relative;
    z-index: 1;

    .container {
      height: 100%;
    }

    .content {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 55%;

      @include phone {
        width: 100%;
      }
    }

    h2 {
      color: $secondary-color;
      font-family: $second-font;
      font-size: 44px;
      font-weight: 500;
      margin-bottom: 40px;
      margin-right: 20px;

      @include phone {
        font-size: 30px;
      }
    }

    p {
      color: #fff;
      font-size: 24px;
    }

    form {
      align-items: center;
      border-bottom: 1px solid #5a6375;
      display: flex;
      justify-content: space-between;
      margin-top: 40px;
      padding-bottom: 10px;
      width: 100%;

      input {
        background: transparent;
        border: none;
        color: #fff;
        font-size: 16px;
        width: 80%;
        &::placeholder {
          color: #5a6375;
        }
      }

      input[type="submit"] {
        background: $secondary-color;
        border: 0;
        border-radius: 50px;
        color: #ffffff;
        font-size: 16px;
        font-weight: bold;
        padding: 10px 20px;
        text-transform: uppercase;
      }

      .wpcf7-not-valid-tip {
        color: #f49a9a;
        font-size: 12px;
        margin-top: 5px;
      }

      input.wpcf7-not-valid {
        // background: #fff0f0;
        border-color: #f49a9a;
        // color: #f49a9a;
      }

      .wpcf7-validation-errors {
        display: none !important;
      }

      .wpcf7-mail-sent-ok {
        border: none;
        color: #fff;
        padding: 0;
      }

      .wpcf7 .screen-reader-response {
        display: none !important;
      }

      @include phone {
        .ajax-loader {
          display: none;
        }

        input[type="submit"] {
          font-size: 14px;
          width: 100%;
        }
      }
    }
  }

  .post {
    display: grid;
    grid-template-columns: 3fr 1fr;
    column-gap: 40px;
    align-items: flex-start;

    margin-bottom: 50px;
    position: relative;
    z-index: 1;

    color: #858a8f;

    .info-title {
      color: #1abdde;
    }

    .block-left .wp-block-media-text__content {
      padding: 0 0 0 8% !important;
    }

    .block-right .wp-block-media-text__content {
      padding: 0 8% 0 0 !important;
    }

    .wp-block-media-text__content p {
      text-align: justify !important;
    }

    .image-title {
      color: #1abdde;
    }

    ul {
      list-style: initial;
      padding-inline-start: 40px;
    }

    .wp-block-gallery {
      padding-inline-start: 0px;
    }

    @include phone {
      grid-template-columns: 100%;
    }

    .intro {
      color: #fff;
      font-family: $second-font;
      font-size: 28px;
      font-weight: 500;
      margin-bottom: 80px;
      width: 30%;

      @include phone {
        width: 100%;
        color: $secondary-color;
      }
    }

    .conteudo__top {
      img {
        width: 100%;
      }

      .date {
        color: $text;
        display: block;
        margin: 15px 0 25px;
      }
    }
  }
}
